@media screen and (max-width: 890px) {
 body{
   margin: 40px 5%;
 }
}

@media screen and (min-width: 1200px) {
  body{
    margin: 50px 20%;
  }
}
.showSuggestions {
  display: block;
}

input[type="text"],
input[type="number"] {
  padding: 8px 6px 8px 10px;
  border-radius: 4px;
  outline: none;
  border: 1px solid #ccc;
  font-size: 14px;
  line-height: 20px;
  align-self: center;
  height: 38px;
}
.num-events{
  width: 80px ;
  text-align: center;
}

.city-input {
  width: 250px;
}

.citySearch {
  display: flex;
  flex-direction: column;
  flex-basis: 300px;
  max-width: 300px;
  margin: auto;
  position: relative;
}
.suggestions {
  width: 250px;
  position: absolute;
  z-index: 2;
  align-self: center;
  margin: 0;
  top: 43px;
  font-size: 14px;
  box-shadow: 10px 10px 16px -12px rgba(0, 0, 0, 0.75);
}

.suggestions > li {
  text-align: left;
  padding: 8px 6px 8px 10px;
  background-color: #eee;
  cursor: default;
}
.suggestions > li:last-child {
  border-bottom-left-radius: 4px;
  border-bottom-right-radius: 4px;
}

.suggestions > li:hover {
  background-color: #bcdcdb;
}
.event {
  background-color: #DBE2EF;
  font-size: 1.1rem;
  padding: 10px 10px 35px;
  margin-bottom: 10px;
  text-align: left;
  border: 1px solid #ccc;
  border-radius: 4px;
  position: relative;
}

.event-title {
  font-size: 1.5rem;
  font-weight: 600;
}

.position-btn {
  position: absolute;
  right: 5px;
  bottom: 5px;
  padding: 5px 8px;
  border-radius: 4px;
  outline: none;
  cursor: pointer;
}
.btn{
  box-shadow:inset 0px 1px 3px 0px #b0b0b0;
  background-color:#3F72AF;
  border-radius:5px;
  border:1px solid #696969;
  cursor:pointer;
  color:#ffffff;
  font-size:15px;
  padding:11px 23px;
	text-decoration:none;
	text-shadow:0px -1px 0px #666666;
}

.btn:hover {
	background-color:#112D4E;
}     

.details{
  margin: 2px 0 17px;
  line-height: 30px;
}
.alert{
  margin:4px auto;
  padding-left: 10px;
  padding-right: 4px;
  border-style: solid;
  border-width: 0 0 0 4px ;
  text-align: center;
  width: 50%;
}

.alert-center{
  margin: auto;
  width: 50% ;
  text-align: center;
}

@media screen and (max-width: 890px) {
  .alert{
    width: 100%;
  }
  .alert-center{
    width: 100%;
  }
 }
.numberOfEvents {
  width: 50%;
  margin: 50px auto;
  text-align: center;
}

/* Make clicks pass-through */
#nprogress {
  pointer-events: none;
}

#nprogress .bar {
  background: #29d;

  position: fixed;
  z-index: 1031;
  top: 0;
  left: 0;

  width: 100%;
  height: 7px;
}

/* Fancy blur effect */
#nprogress .peg {
  display: block;
  position: absolute;
  right: 0px;
  width: 100px;
  height: 100%;
  box-shadow: 0 0 10px #29d, 0 0 5px #29d;
  opacity: 1.0;

  -webkit-transform: rotate(3deg) translate(0px, -4px);
          transform: rotate(3deg) translate(0px, -4px);
}

/* Remove these to get rid of the spinner */
#nprogress .spinner {
  display: block;
  position: fixed;
  z-index: 1031;
  top: 30px;
  right: 30px;
}

#nprogress .spinner-icon {
  width: 40px;
  height: 40px;
  box-sizing: border-box;

  border: solid 2px transparent;
  border-top-color: #29d;
  border-left-color: #29d;
  border-radius: 70%;

  -webkit-animation: nprogress-spinner 400ms linear infinite;
          animation: nprogress-spinner 400ms linear infinite;
}

.nprogress-custom-parent {
  overflow: hidden;
  position: relative;
}

.nprogress-custom-parent #nprogress .spinner,
.nprogress-custom-parent #nprogress .bar {
  position: absolute;
}

@-webkit-keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); }
}
@keyframes nprogress-spinner {
  0%   { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  100% { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

